import DropDown from './components/drop-down';
import Form from './components/form';
import ProductMinitature from './components/product-miniature';
import ProductSelect from './components/product-select';
import TopMenu from './components/top-menu';

import prestashop from 'prestashop';
import EventEmitter from 'events';

import './lib/bootstrap-filestyle.min';
import './lib/jquery.scrollbox.min';
import './lib/jquery.bxslider-rahisified';

import './components/block-cart';
import './components/cartes-custom';

import '../css/theme';
import './responsive';
import './checkout';
import './customer';
import './listing';
import './product';
import './cart';
import './jPushMenu';


// "inherit" EventEmitter
for (var i in EventEmitter.prototype) {
  prestashop[i] = EventEmitter.prototype[i];
}

function initPhTestimonials()
{

    if(typeof testimonialsAutoPlay !== 'undefined' && testimonialsAutoPlay !== null){
        setInterval(changeSlide, testimonialsAutoPlay);
    }

}

function changeSlide(){
    var currentSlide = $('.testimonials-carousel-item.active');
    var nextSlide = $('#slideTesti-'+currentSlide.data('next'));
    currentSlide.fadeOut('slow', function() {
        currentSlide.removeClass('active');
        nextSlide.addClass('active');
        nextSlide.fadeIn('slow');
    });



}

function nextPost(){
    var currentSlide = $('.post-slide.active');
    var nextSlide = $('#slidePost-'+currentSlide.data('next'));
    currentSlide.fadeOut('slow', function() {
        currentSlide.removeClass('active');
        nextSlide.addClass('active');
        nextSlide.fadeIn('slow');
    });

}

function prevPost(){
    var currentSlide = $('.post-slide.active');
    var nextSlide = $('#slidePost-'+currentSlide.data('prev'));
    currentSlide.fadeOut('slow', function() {
        currentSlide.removeClass('active');
        nextSlide.addClass('active');
        nextSlide.fadeIn('slow');
    });

}

var sliders = {};
$(document).ready(() => {
  let dropDownEl = $('.js-dropdown');
  const form = new Form();
  let topMenuEl = $('.js-top-menu ul[data-depth="0"]');
  let dropDown = new DropDown(dropDownEl);
  let topMenu = new TopMenu(topMenuEl);
  let productMinitature = new ProductMinitature();
  let productSelect  = new ProductSelect();
  dropDown.init();
  form.init();
  topMenu.init();
  productMinitature.init();
  productSelect.init();
  initPhTestimonials();
  $('#prevPost').click(prevPost);
  $('#nextPost').click(nextPost);


  $('#contactCartesPerso').submit(function(e){
    e.preventDefault();
    e.stopPropagation();

    var url = $('#contactCartesPerso').attr('action');
    var method = $('#contactCartesPerso').attr('method');
   
    var message = "Demande de devis cartes imprimées\n\n"+
                    'Nom : '+$('#contactCartesPerso').find('#nom').val()+'\n'+
                    'Prénom : '+$('#contactCartesPerso').find('#prenom').val()+'\n'+
                    'eMail : '+$('#contactCartesPerso').find('#email').val()+'\n'+
                    'Téléphone : '+$('#contactCartesPerso').find('#telephone').val()+'\n'+
                    "Message : \n"+$('#contactCartesPerso').find('#message').val();

    $('#contactCartesPerso').find('#message').val(message);

    var data = $('#contactCartesPerso').serialize()+'&submitMessage=true';
    

    $.ajax({
        type:method,
        url: url,
        data: data,
        success:function(data){
            var previousText = $('#contactCartesPerso').find('#submitButton').val();
            $('#contactCartesPerso').find('#submitButton').addClass('sent').val('Demande envoyée ;-)');
            setTimeout(function(){
                $('#contactCartesPerso').find('#submitButton').removeClass('sent').val(previousText);
            }, 10000);
        }
    })
  });



  function activateSlider(element){
      var width = $(window).width();
      var count=  element.children().length;


      if((count === 4 && width < 1100) || (count === 3 && width < 768) || (count === 2 && width < 544) || count <= 1){

          element.bxSlider({
              slideMargin: 1,
              autoReload: true,
              breaks: [{screen:0, slides:1, pager:false},{screen:544, slides:2, pager:false},{screen:768, slides:3, pager:false},{screen:1100, slides:4, pager:false}],
              slideSelector: '.product-miniature',
              moveSlides: 1
          });
      }

  }
    var width = $(window).width();

    $(".toggle-menu").jPushMenu({closeOnClickLink: false, closeOnClickOutside:false});

    $("#contentTabBestDeals .titleCat").html($("#tabsBestDeals .nav-link.active").data('cat'));


        $('#tabsBestDeals .nav-link').each(function (e) {
            var $catBdId = $(this).data('catid');
            $(this).click(function (e) {
                $("#contentTabBestDeals .titleCat").html($(this).data('cat'));
            });
            var count = $('#bxsliderBestDeals' + $catBdId).children('.product-miniature').length;
            if((count === 4 && width < 1100) || (count === 3 && width < 768) || (count === 2 && width < 544) || count > 2) {
                sliders["bd"+$catBdId] = $('#bxsliderBestDeals' + $catBdId).bxSlider({
                    slideMargin: 1,
                    autoReload: true,
                    breaks: [{screen: 0, slides: 1, pager: false}, {screen: 544, slides: 2, pager: false}, {screen: 768, slides: 2, pager: false }, {screen: 1100, slides: 3, pager: false}, {screen: 1300, slides: 4, pager: false}],
                    slideSelector: '.product-miniature',
                    moveSlides: 1
                });
            }
        });


    $('#tabsFeatured .nav-link').each( function (e) {

        var $catFId = $(this).data('catid');
        var count = $('#bxsliderFeatured'+$catFId).children('.product-miniature').length;
        if((count === 4 && width < 1200) || (count === 3 && width < 1000) || (count === 2 && width < 544) || count > 4) {
             sliders["featured"+$catFId] = $('#bxsliderFeatured' + $catFId).bxSlider({
                slideMargin: 1,
                autoReload: true,
                breaks: [{screen: 0, slides: 1, pager: false}, {screen: 544, slides: 2, pager: false}, {
                    screen: 768,
                    slides: 3,
                    pager: false
                }, {screen: 1100, slides: 4, pager: false}],
                slideSelector: '.product-miniature',
                moveSlides: 1
            });
        }
    });


    var menuInitialPosition = 0;
    var ref = $('#menuHeader').position();
    var refTop = $(window).scrollTop();
    $(window).scroll(function () {
        //Fixed Menu Events
        var ref = $('#menuHeader').offset();
        var refTop = ref.top;
        var pos = $(window).scrollTop();
        var documentWidth = $(window).width();
        if (documentWidth > 991) {
            if (pos <= menuInitialPosition) {
                $('#menuHeader').removeClass('fixed-top').removeAttr('style');
                menuInitialPosition = 0;
            } else if (pos > refTop + 200) {
                $('#menuHeader').addClass('fixed-top').animate({
                    top: 0
                }, 1500);
                menuInitialPosition = refTop;
            }
        }
    });




    $('#tabsFeatured .nav-link').on('shown.bs.tab', function (e) {
        var $catFId = $(this).data('catid');
        if(sliders["featured"+$catFId]!==undefined){
            sliders["featured"+$catFId].reloadSlider();
        }
    });

    $('#tabsBestDeals .nav-link').on('shown.bs.tab', function (e) {
        var $catFId = $(this).data('catid');
        if(sliders["bd"+$catFId]!==undefined){
            sliders["bd"+$catFId].reloadSlider();
        }

    });

    function jqUpdateSizePage() {


    }

    jqUpdateSizePage();    // When the page first loads
    $(window).resize(jqUpdateSizePage);

    $(".btSearchMobile").click(function(e){
        e.stopPropagation();
        e.preventDefault();

        if ($(this).hasClass("active")) {
            $(this).removeClass("active");
            $("#search_widget").removeClass("opened");
        } else {
            $(this).addClass("active");
            $("#search_widget").addClass("opened");
        }
    });



    if($('body').hasClass('page-contact')) {
       $('.contact-form form').submit( (event) => {
	        let subject = $('#subject').val();
          $('#message').val('Sujet : '+subject+'\n\n'+'Message : \n'+$('#message').val());
             gtag('event', 'conversion', {'send_to': 'AW-1069635118/PHdICKzgxIgBEK6shf4D'})
          return true;
	      });
    }

    $('.js-scrollTo').on('click', function() { // Au clic sur un élément
        var page = $(this).attr('href'); // Page cible
        var speed = 750; // Durée de l'animation (en ms)
        $('html, body').animate( { scrollTop: $(page).offset().top }, speed ); // Go
        return false;
    });

});



